.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.loader {
  height: 100%;
  width: 100%;
  z-index: 9999;
  background: #ffffff40;
  position: fixed;
  inset: 0;
}

.loader .loade-inner {
  clip: rect(0, 80px, 80px, 40px);
  height: 80px;
  width: 80px;
  animation: 1.5s linear infinite animate;
  position: absolute;
  top: calc(50% - 40px);
  left: calc(50% - 40px);
}

@keyframes animate {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(220deg);
  }
}

.loader .loader-inner:after {
  clip: rect(0, 80px, 80px, 40px);
  content: "";
  height: 80px;
  width: 80px;
  border-radius: 50%;
  animation: 1.5s ease-in-out infinite animate2;
  position: absolute;
  top: 50%;
  left: 50%;
}

@keyframes animate2 {
  0% {
    transform: rotate(-140deg);
    box-shadow: inset 0 0 0 17px #ff6b08a9;
  }

  50% {
    box-shadow: inset 0 0 0 2px #ff6b08a9;
  }

  100% {
    transform: rotate(140deg);
    box-shadow: inset 0 0 0 17px #ff6b08a9;
  }
}

